import inherits from 'inherits';

import CommandInterceptor from 'diagram-js/lib/command/CommandInterceptor';

/**
 * Абстрактный провайдер, позволяющий создавать
 * свой порядок элементов диаграммы на холсте.
 *
 * Он убеждается в том, что порядок всегда сохраняется
 * при создании элемента и его перемещении.
 *
 * Для его работы, расширяется метод {@link OrderingProvider#getOrdering}.
 *
 * @example
 *
 * ```javascript
 * function CustomOrderingProvider(eventBus) {
 *   OrderingProvider.call(this, eventBus);
 *
 *   this.getOrdering = function(element, newParent) {
 *     // всегда вставлять элементы поверх
 *     // при перемещении
 *     return {
 *       index: 0,
 *       parent: newParent
 *     };
 *   };
 * }
 * ```
 *
 * @param {EventBus} eventBus
 */
export default function OrderingProvider (eventBus) {
  CommandInterceptor.call(this, eventBus);

  const self = this;

  this.preExecute(['shape.create', 'connection.create'], function (event) {
    const context = event.context;
    const element = context.shape || context.connection;
    const parent = context.parent;

    const ordering = self.getOrdering(element, parent);

    if (ordering) {
      if (ordering.parent !== undefined) {
        context.parent = ordering.parent;
      }

      context.parentIndex = ordering.index;
    }
  });

  this.preExecute(['shape.move', 'connection.move'], function (event) {
    const context = event.context;
    const element = context.shape || context.connection;
    const parent = context.newParent || element.parent;

    const ordering = self.getOrdering(element, parent);

    if (ordering) {
      if (ordering.parent !== undefined) {
        context.newParent = ordering.parent;
      }

      context.newParentIndex = ordering.index;
    }
  });
}

inherits(OrderingProvider, CommandInterceptor);

OrderingProvider.$inject = [
  'eventBus'
];

/**
 * Возвращает заданный порядок элемента, в его числе
 * родительский элемент и индекс в новом родительском элементе
 *
 * Поскольку требуется, чтобы Frame находился под всеми элементами,
 * ему задается zIndex равный отрицательной бесконечности.
 *
 * Имплементация данного метода должна возвращать объект,
 * состоящий из `родителя` и `индекса`;
 *
 * @param {djs.model.Base} element
 * @param {djs.model.Shape} newParent
 *
 * @return {Object} описание порядка
 */
OrderingProvider.prototype.getOrdering = function (element, newParent) {
  const zIndexOrder = newParent.children.map(child => child.zIndex || 0).sort();
  const zIndex = zIndexOrder.findIndex(index => index === element.zIndex || 0);
  const index = element.isFrame ? -Infinity : zIndex;

  return {
    index: index,
    parent: newParent
  };
};
